.About {
    margin: 26px auto;
    padding: 50px;
    color: #323231;
    margin-top: auto;
    font-family: 'Playfair Display', serif;
    margin-bottom: -50px;
}
 
.About-img {
    border: 1px solid #9099A2;
    box-shadow: 0 2px 9px #9099A2;
    border-radius: 4px;
    padding: 5px;
    width: 200px;
    float: left;
    margin-right: 20px;
}

.About-text {
  margin-left: 300px;
  /* word-wrap: break-word; */
}

@media only screen and (max-width: 600px){
    .About-text {
      margin-left: 10px;
      font-size: 5vw;
    }
    .About-img {
      width: 120px;
      margin-top: 30px;
    }
    .animated {
      font-size: 100%;
    }
  }
